import React, {
  useCallback, useLayoutEffect, useMemo, useRef, useState
} from 'react';
import {
  Button, Box, FormControl, FormLabel, Input
} from '@chakra-ui/react';
import { useTranslation } from 'react-i18next';
import { shallowEqual } from 'react-redux';
import Inputmask from 'inputmask';
import MiniBox from '../../../layout/MiniBox';
import { useAppAsyncDispatch, useAppSelector } from '../../../hooks/store';
import { activate } from '../../../store/middlewares/card';
import CardExpiryInput from '../../../components/CardExpiryInput';
import { apiErrorHandler } from '../../../utils/general';
import { useDrawer } from '../../../hooks/drawer';

const mask = '9999';
const CardActivated = React.lazy(() => import('../CardActivated/CardActivated'));

const CardActivation = () => {
  const { t } = useTranslation();
  const { open, goBack } = useDrawer<{cardId: string}>();
  const dispatch = useAppAsyncDispatch();
  const [formData, updateFormData] = useState({
    last4: '',
    expirationDate: ''
  });

  const selectedCard = useAppSelector((store) => store.card.selectedCard, shallowEqual);

  const last4input = useRef(null);

  const isValid = useMemo(() => formData.last4.length === 4 && formData.expirationDate.length === 7, [formData]);

  useLayoutEffect(() => {
    if (last4input.current) {
      Inputmask({
        mask, showMaskOnHover: false, showMaskOnFocus: false, jitMasking: true
      }).mask(last4input.current);
    }
  }, []);

  const onSubmit = useCallback(() => {
    if (selectedCard?.id) {
      const [expiryMonth, expiryYear] = formData.expirationDate.split('/');
      dispatch(activate(selectedCard?.id, {
        expiryMonth,
        expiryYear,
        last4: formData.last4
      })).then(() => {
        open(CardActivated);
      }).catch(apiErrorHandler);
    }
  }, [selectedCard, formData]);

  if (!selectedCard) {
    return null;
  }

  return (
    <div className="page">
      <MiniBox
        heading={t('activate_card.title')}
        footer={(
          <Button width="100%" variant="primary" disabled={!isValid} onClick={onSubmit}>
            {t('activate_card.ctaText')}
          </Button>
)}
        showBack
        onBack={goBack}
      >
        <Box width="100%" paddingTop="15px">
          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('activate_card.last4')}
            </FormLabel>
            <Input
              ref={last4input}
              value={formData.last4}
              variant="primary"
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => updateFormData({
                ...formData,
                last4: e.target.value
              })}
            />
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('activate_card.expiry')}
            </FormLabel>
            <CardExpiryInput
              value={formData.expirationDate}
              onChange={(v) => updateFormData({
                ...formData,
                expirationDate: v
              })}
            />
          </FormControl>
        </Box>
      </MiniBox>

    </div>
  );
};

export default CardActivation;
